import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {
	Button,
	Tooltip,
	Card,
	Col,
	DatePicker,
	Drawer,
	Form,
	Input,
	message,
	Modal,
	Row,
	Select,
	Table,
	Divider
} from "antd";
import {
	DeleteOutlined,
	EditOutlined, FilterOutlined, PlusOutlined,
	ReloadOutlined, SettingOutlined
} from "@ant-design/icons";
import {useHistory} from 'react-router-dom'
import ApiService from "services/api.service";
import moment from "moment/moment";
import TypeStatusAirlinesForm from "../../../../../components/forms/type-status-airlines.form";
import AirlinesSelectForm from "../../../../../components/forms/airlines.select.form";
import CitySelectForm from "../../../../../components/forms/city-select.form";
import ButtonPermission from "../../../../../../../components/permission-components/button.permission";

function mapStateToProps(state) {
	return {};
}

function Index() {
	
	const history = useHistory();
	
	const defaultColumns = [
		{
			visible: true,
			key: "no",
			title: "No",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			visible: true,
			key: "day",
			dataIndex: "day",
			title: "Day",
			render: (_) => _ ?? "-"
		},
		{
			visible: true,
			key: "type",
			dataIndex: "type",
			title: "Type",
			render: (_) => _ ?? "-"
		},
		{
			visible: true,
			key: "airline",
			dataIndex: ['airline', 'opt_value', 'name'],
			title: "Airlines",
			render: (_, val) => _ ?? val?.airline_id ?? "-"
		},
		{
			visible: true,
			key: "flight_no",
			title: "Flight No",
			dataIndex: "flight_no"
		},
		{
			visible: true,
			key: "city",
			dataIndex: ['city', 'opt_value', 'name'],
			title: "From / To",
			render: (_) => _ ?? "-"
		},
		{
			visible: true,
			key: "schedule_time",
			dataIndex: "schedule_time",
			title: "Schedule Time",
			render: (_, val) => {
				return moment(_).format("HH:mm")
			}
		},
		{
			visible: true,
			key: 'action',
			title: "Action",
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2 justify-content-end'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							disabled={loadings.length > 0 ? true : _?.isDeleted ? true : data.some((item) => {
								return typeof (item?.isDeleted) !== 'undefined' ? item?.isDeleted === true : false
							})}
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	];
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 10
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	const [loadings, setLoadings] = useState([])
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	function _caller() {
		setLoading(true)
		new ApiService({
			url: "/api/v1/flight/schedule",
			mode: "express",
			config: {
				params: {
					...params
				}
			}
		})
			.get()
			.then((response) => {
				response.data = response.data.map((item) => ({
					...item,
					schedule_time: moment(item?.schedule_time, 'HH:mm').isValid() ? moment(item?.schedule_time, 'HH:mm') : item?.schedule_time
				}))
				setData([...response?.data])
				setPagination({
					...pagination,
					...response?.pagination,
					...response?.paginate
				})
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	useEffect(() => {
		_caller()
	}, [params])
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				Reflect.set(value, 'schedule_time', moment(value?.schedule_time).isValid() ? moment(value?.schedule_time).format("HH:mm") : value?.schedule_time)
				new ApiService({
					body: value,
					url: `/api/v1/flight/schedule/${selected?.id}`,
					mode: "express"
				}).put()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.info(JSON.stringify(err))
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	function handleClickEdit(_, index) {
		
		setVisible(!visible)
		let newField = _ ?? {}
		// Reflect.set(newField,'schedule_time',moment(newField?.schedule_time,'HH:mm').isValid() ? moment(newField?.schedule_time,'HH:mm') : null)
		Reflect.set(newField, 'airline_id', newField?.airline?.id ?? _?.airline_id)
		Reflect.set(newField, 'origin_id', newField?.city?.id ?? _?.origin_id)
		form.resetFields()
		Object.freeze(newField)
		setSelected(newField)
		
	}
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)
		
	}
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/flight/schedule/${_}`,
			mode: "express",
		}).delete()
			.then((response) => {
				if (!response?.error) {
					if (data && typeof (data[index]) !== 'undefined') {
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...data.filter((item) => item.id !== _)])
						setLoadings([])
					}, 2000)
				} else {
					enterLoading(index, false)
					setLoadings([])
					message.error(response?.message ?? "Error: delete by id " + _)
				}
				
			})
			.catch((err) => {
				enterLoading(index, false)
				setLoadings([])
			})
	}
	
	const onChangeDrawer = (val) => {
		setVisibleDrawer(val ?? false)
	}
	
	
	const extra = (
		<div className={'d-flex align-items-center mk-gap-2'}>
			<Tooltip title={'Reload Data'}>
				<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading} size={'small'}/>
			</Tooltip>
			<Tooltip title={'Setting'}>
				<Button icon={<SettingOutlined/>} type={'primary'} ghost onClick={() => onChangeDrawer(true)} size={'small'}/>
			</Tooltip>
		</div>
	)
	
	const [columns, setColumns] = useState(defaultColumns ?? [])
	
	const onColumnsChange = (value) => {
		setColumns(() => {
			return defaultColumns.filter((item) => value.includes(item.key))
		})
	}
	return (
		<React.Fragment>
			<Drawer
				title={`Filter`}
				placement={"right"}
				closable
				onClose={() => onChangeDrawer(false)}
				visible={visibleDrawer}
			>
				<div className={'w-100'}>
					<h5>Table Column</h5>
					<Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
					        defaultValue={defaultColumns.map((item) => item?.key) ?? []}>
						{
							defaultColumns.map((item) => (
								<Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
							))
						}
					</Select>
					
					<Divider/>
				</div>
			</Drawer>
			
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{
					...selected,
				}} layout={'vertical'}>
					<Row gutter={24}>
						<Col span={8}>
							<Form.Item hasFeedback name={'day'} label={'Day'} rules={[{required: true, message: "Can't be empty"}]}>
								<Select placeholder={'select one day'}>
									{[
										"Sunday",
										"Monday",
										"Tuesday",
										"Wednesday",
										"Thursday",
										"Friday",
										"Saturday"
									].map((item) => {
										return <Select.Option key={item} value={item}>{item}</Select.Option>
									})}
								
								</Select>
							</Form.Item>
						</Col>
						<Col span={8}>
							<TypeStatusAirlinesForm
								keyValue={['data', 'name']}
								name={'type'} label={'Type'}/>
						</Col>
						<Col span={8}>
							<AirlinesSelectForm
								initialValue={selected?.airline_id ?? selected?.airline?.id ?? null}
								name={'airline_id'}
								label={'Airlines'}
								keyValue={['id']}
								keyDisplay={['data', 'name']}
								select={{
									placeholder: "Select your airlines"
								}}
								rules={[
									{
										required: true,
										message: "Can't be empty!"
									}
								]}
							/>
						</Col>
						<Col span={12}>
							<Form.Item
								hasFeedback
								name={'flight_no'}
								label={'Flight Number'}
								rules={[
									{
										required: true,
										message: "Can't be empty!"
									}
								]}
							>
								<Input
									placeholder={'Input your flight number'}
									autoComplete={'off'}
								/>
							</Form.Item>
						</Col>
						<Col span={12}>
							<CitySelectForm
								name={'origin_id'}
								initialValue={selected?.city?.id ?? selected?.origin_id ?? null}
								label={'To / Destination'}
								keyValue={['id']}
								displayValue={['data', 'name']}
								select={{
									placeholder: "Select your To/Destination"
								}}
								rules={[
									{
										required: true,
										message: "Can't be empty!"
									}
								]}
							/>
						</Col>
						<Col span={12}>
							<Form.Item
								hasFeedback
								name={'schedule_time'}
								label={"Schedule Time"}
								rules={[
									{
										required: true,
										message: "Can't be empty!"
									}
								]}>
								<DatePicker.TimePicker
									format={'HH:mm'}
									style={{
										width: "100%"
									}}
								/>
							</Form.Item>
						</Col>
					</Row>
					{/*<Form.Item name={'identifier'} hidden label={'Identifier'} rules={[{required: true, message: "Can't be empty!"}]}>*/}
					{/*	<Input/>*/}
					{/*</Form.Item>*/}
					{/*<Form.Item name={['data', 'code']} label={'Code'} rules={[{required: true, message: "Can't be empty!"}]}>*/}
					{/*	<Input/>*/}
					{/*</Form.Item>*/}
					{/*<Form.Item name={['data', 'name']} label={'Display Name'} rules={[{required: true, message: "Can't be empty!"}]}>*/}
					{/*	<Input/>*/}
					{/*</Form.Item>*/}
				</Form>
			</Modal>
			
			
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Display', 'Schedule']}
				thumbnail={null}
				extra={<div className={'d-flex align-items-center mk-gap-2'}>
					<ButtonPermission
					permission={['public','admin']}
					theme={{
					type:"disabled",
					property:{
						message:"Access Denied"
					}
				}}
					config={{
					onClick: function(){
						history.push('/dashboard/display/schedule/create')
					},
					ghost:true,
					icon:<PlusOutlined/>,
					// icon: PlusOutlined,
					type:"primary",
					size:"small"
				}}>
					Add New
				</ButtonPermission>
				</div>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false} extra={extra}>
						<Table
							loading={loading}
							dataSource={data}
							rowClassName={(record, index) => record?.isDeleted ? "!mk-bg-red-500 !mk-bg-opacity-20" : ''}
							columns={columns}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);